* {
	transition-duration: 0.15s;
}

body {
	background-color: teal;
	font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
	color: black;
	-webkit-user-select: none;
	        user-select: none;
	margin: 0;
}

h1 {
	font-size: xxx-large;
	margin: 0;
	padding-top: 5px;
	padding-bottom: 5px;
}

input[type="text"]:focus, textarea:focus {
	outline: none;
}
#site-header {
	text-align: center;
	background-color: burlywood;
	padding-bottom: 10px;
	margin-bottom: 10px;
}

.new-task-form label, input {
	display: block;
	margin: auto;
	margin-bottom: 10px;
}

.new-task-form input[type="text"] {
	width: 80%;
	max-width: 657px;
	text-align: center;
}

.create-task-button {
	background-color: rgb(62, 113, 113);
	color: white;
	cursor: pointer;
	border: none;
	border-radius: 5px;
	margin-bottom: 5px;
	padding: 10px;
	font-size: medium;
	display: inline-block;
	margin-right: 20px;
	width: 140px;
}

.create-task-button:hover {
	background-color: rgb(52, 93, 93);
}

.dropdown {
	position: relative;
	display: inline-block;
}

.dropdown-button {
	background-color: #e4642c;
	color: white;
	border: none;
	border-radius: 5px;
	padding: 10px;
	font-size: medium;
	display: inline-block;
	width: 140px;
}

.dropdown-content {
	display: none;
	position: absolute;
	border-radius: 5px;
	background-color: #f1f1f1;
	box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
	cursor: pointer;
	z-index: 1;
	min-width: 80px;
	width: 100%;
}

.dropdown-content p {
	color: black;
	display: block;
	text-align: left;
	padding: 10px;
	width: 85.5%;
	margin: 0;
}

.dropdown-content p:hover {
	background-color: #ddd;
}

.dropdown-content #first-in-list {
	border-radius: 5px 5px 0px 0px;
}

.dropdown-content #last-in-list {
	border-radius: 0px 0px 5px 5px;
}

.dropdown:hover .dropdown-content {
	display: block;
}

.dropdown:hover .dropdown-button {
	background-color: #c7592a;
}

.todo-list {
	width: 80%;
	max-width: 657px;
	margin: auto;
	padding: 0;
}

.todo-list li {
	list-style-type: none;
}
.todo-list .task-container {
	margin-bottom: 10px;
	margin-left: 0px;
	border: 2px solid black;
	border-radius: 5px;
	display: flex;
	background-color: navajowhite;
}

.todo-list .text-fields {
	width: 73%;
}

.todo-list .buttons {
	width: 27%;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
}

.todo-list .task-name {
	padding: 7px 0px 0px 5px;
}

.todo-list hr.horizontal {
	border-width: 1px 0 0 0;
	border-color: black;
	width: 100%;
}

.todo-list .vertical-line {
	width: 0.5px;
	background-color: black;
}

.todo-list .task-desc textarea {
	border: none;
	background-color: transparent;
	margin-bottom: 7px;
	padding: 0px 5px;
	overflow: hidden;
	overflow-wrap: break-word;
	resize: none;
	width: 98%;
	line-height: 1.5;
}

.todo-list .task-desc textarea:placeholder-shown {
	font-style: italic;
}

.todo-list .task-desc textarea::selection {
	background: slategray;
	color: white;
}

.todo-list button {
	background-position: center;
	border-radius: 100%;
	width: 35px;
	height: 35px;
	cursor: pointer;
	background-size: 21px;
	background-color: white;
	background-repeat: no-repeat;
}

.todo-list button:hover {
	border-color: white;
}

.todo-list .priority-button {
	color: #FFFFFF;
	font-size: x-large;
	font-weight: bold;
}

.todo-list .check-button {
	background-image: url(/todo-list/static/media/check_icon.2582aef8.jpg);
}

.todo-list .delete-button {
	background-image: url(/todo-list/static/media/trash_icon.9177df39.jpg);
	background-size: cover;
}
